import React from 'react'
import Ftwhite from './images/ftwhite.png'
import Footerfb from './images/footerfb.png'
import Footerig from './images/footerig.png'
import Footert from './images/footert.png'
import Footerl from './images/footerl.png'
import "./Footer.css"

const Footer = () => {
  return (
    <footer> 
      <div className="fbg">

        <div className='container'> 
      <div className="fflex">

         <div className="firstaxis">
      <img src={Ftwhite} className="ftwhitelogo" width="120px" alt=""/>  
      <p> We take local ideas to the global <br/> stage by building sustainable software <br/>for ease</p>
      </div>

       
       <div className="secondaxis">
      
      <ul> 
        <h2> Company </h2> 
        <li> About </li> 
        <li> Products </li> 
        <li> Contact us </li> 
      </ul>
      </div>

      <div className="secondaxis">
      <ul> 
        <h2> Contact </h2> 
        <img src={Footerfb} alt="" width="30px"/>
        <img src={Footerig} alt="" width="30px"/>
        <img src={Footert} alt="" width="30px"/>
        <img src={Footerl} alt="" width="30px"/>
        <li> Support@fexteller.com </li> 
        <li> The business address goes here. </li>  
      </ul>
      </div>

      
       <div className="secondaxis">
      
      <ul> 
        <h2> Oursourcing </h2> 
        <li> Designer</li> 
        <li> Developer </li> 
        <li> Copywriter </li> 
        <li> Business Enablers </li> 
        <li> Digital Marketer </li>
      </ul>
      </div>


      </div>
      </div>
      </div> 
      </footer>       
  )
}

export default Footer