import React from 'react'
import { useState, useRef, useEffect } from 'react'
import logo from './images/ft.png'
import "./Navsecond.css";

function Navsecond() {

   const [sidebar, setSidebar] = useState(false)

   let Icons = useRef()

   useEffect(() => {
     if(sidebar){
       Icons.current.style.display = "none";
     }
   }, [])

  function Showmenu() {
      setSidebar(!sidebar)
   }

  return (
     <>
      <div className="container"> 
      <div className="nav_flex2"> 
      <div className="logo">
      <img src={logo} alt="" width="170px"/> 
      </div>

      <div className="nav_list" onClick={Showmenu}>
          <i className={sidebar ? 'fas fa-times fa-xl' : 'fas fa-bars fa-xl'} > </i>
     </div>
     </div> 

     <div class="container">
     <div className={sidebar ? "nav-menu active" : "nav-menu"}> 
     <ul>
         <li><a href="/">  Home  </a> </li>
         <li> <a href="/About">  About us </a> </li>
         <li>  <a href="/products"> Products </a> </li>
         <li> <a href="/contact"> Contact us </a></li>
           </ul> 
     </div>
     </div>
     </div>
    </>
  )
}

export default Navsecond;