import React from 'react'

const Form = (props) => {
   console.log(Form.name)
  const {Name, placeholder, Value, handleClick} = props;
  
  return (
     <> 
     
     <div className="formbg">
        <form> 
          
           <input  name={Name} type="text" id="formname" placeholder={placeholder} value={Value} onChange={handleClick} />
       
        </form>
     
      </div>
     </>
  )
   
}

export default Form