  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
  import sdimage from './images/mbfexteller.gif'
  import world from './images/world.gif'
  import arrow from './images/arrow.png'
  import robot from './images/robot.png'
  import robot2 from './images/peacerobot.png'
  import outsource from './images/outsource.png'
  import support from './images/support.gif'
  import Footer from './Footer.js'
  import { gsap, TweenMax, Power3, Bounce} from 'gsap'
  
  import { useEffect, useRef, useState } from "react";
  import { ScrollTrigger } from 'gsap/ScrollTrigger';
  gsap.registerPlugin(ScrollTrigger);
 
  


const Cards= [
   {
      Img: <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 512"> <path fill="#1a1a4a" d="M128 96h384v256h64V80C576 53.63 554.4 32 528 32h-416C85.63 32 64 53.63 64 80V352h64V96zM624 384h-608C7.25 384 0 391.3 0 400V416c0 35.25 28.75 64 64 64h512c35.25 0 64-28.75 64-64v-16C640 391.3 632.8 384 624 384zM365.9 286.2C369.8 290.1 374.9 292 380 292s10.23-1.938 14.14-5.844l48-48c7.812-7.813 7.812-20.5 0-28.31l-48-48c-7.812-7.813-20.47-7.813-28.28 0c-7.812 7.813-7.812 20.5 0 28.31l33.86 33.84l-33.86 33.84C358 265.7 358 278.4 365.9 286.2zM274.1 161.9c-7.812-7.813-20.47-7.813-28.28 0l-48 48c-7.812 7.813-7.812 20.5 0 28.31l48 48C249.8 290.1 254.9 292 260 292s10.23-1.938 14.14-5.844c7.812-7.813 7.812-20.5 0-28.31L240.3 224l33.86-33.84C281.1 182.4 281.1 169.7 274.1 161.9z"/></svg>,
      Title: "Software Development",
      Description:<p> "We develop on-demand products for different  economical sectors in and across Africa."</p> ,

   }, 

   {
      Img: <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"> <path fill="#1a1a4a" width="10px" d="M156.6 384.9L125.7 353.1C117.2 345.5 114.2 333.1 117.1 321.8C120.1 312.9 124.1 301.3 129.8 288H24C15.38 288 7.414 283.4 3.146 275.9C-1.123 268.4-1.042 259.2 3.357 251.8L55.83 163.3C68.79 141.4 92.33 127.1 117.8 127.1H200C202.4 124 204.8 120.3 207.2 116.7C289.1-4.07 411.1-8.142 483.9 5.275C495.6 7.414 504.6 16.43 506.7 28.06C520.1 100.9 516.1 222.9 395.3 304.8C391.8 307.2 387.1 309.6 384 311.1V394.2C384 419.7 370.6 443.2 348.7 456.2L260.2 508.6C252.8 513 243.6 513.1 236.1 508.9C228.6 504.6 224 496.6 224 488V380.8C209.9 385.6 197.6 389.7 188.3 392.7C177.1 396.3 164.9 393.2 156.6 384.9V384.9zM384 167.1C406.1 167.1 424 150.1 424 127.1C424 105.9 406.1 87.1 384 87.1C361.9 87.1 344 105.9 344 127.1C344 150.1 361.9 167.1 384 167.1z"/></svg>,
      Title: "For Startups",
       Description:<p> New entrepreneurs now have the ability to boost their start-ups with technological solutions. Your next business idea has a better chance of success when you leverage technology.
        </p>

   }, 

   {
      Img: <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 512"><path fill="#1a1a4a" d="M319.9 320c57.41 0 103.1-46.56 103.1-104c0-57.44-46.54-104-103.1-104c-57.41 0-103.1 46.56-103.1 104C215.9 273.4 262.5 320 319.9 320zM369.9 352H270.1C191.6 352 128 411.7 128 485.3C128 500.1 140.7 512 156.4 512h327.2C499.3 512 512 500.1 512 485.3C512 411.7 448.4 352 369.9 352zM512 160c44.18 0 80-35.82 80-80S556.2 0 512 0c-44.18 0-80 35.82-80 80S467.8 160 512 160zM183.9 216c0-5.449 .9824-10.63 1.609-15.91C174.6 194.1 162.6 192 149.9 192H88.08C39.44 192 0 233.8 0 285.3C0 295.6 7.887 304 17.62 304h199.5C196.7 280.2 183.9 249.7 183.9 216zM128 160c44.18 0 80-35.82 80-80S172.2 0 128 0C83.82 0 48 35.82 48 80S83.82 160 128 160zM551.9 192h-61.84c-12.8 0-24.88 3.037-35.86 8.24C454.8 205.5 455.8 210.6 455.8 216c0 33.71-12.78 64.21-33.16 88h199.7C632.1 304 640 295.6 640 285.3C640 233.8 600.6 192 551.9 192z"/></svg>,
      Title: "Hire from Fexteller",
      Description: <p> You have an idea, but not a team for execution? We have a team here at Fexteller ready to build and colaborate with you </p>
   }
]
 
 
 const Homepage = () => {
   let Animate1 = useRef()
   let Animate2 = useRef()
   let Animate3 = useRef()
   let Animate4 = useRef()
   let Animate5 = useRef()

   useEffect(() => {
     TweenMax.to(
        Animate1,
        {
           opacity:1,
           duration:3,
           y: -30,
           ease : Power3.easeOut,
        }
     )

      TweenMax.to(
        Animate2,
        {
           opacity:1,
           duration:3,
           y: -30,
           delay: .2,
           ease : Power3.easeOut,
        }
     )

      TweenMax.to(
        Animate3,
        {
           opacity:1,
           duration:3,
           y: -30,
           delay: .4,
           ease : Power3.easeOut,
        }
     )

      TweenMax.to(
        Animate4,
        {
           opacity:1,
           duration:3,
           y: -30,
           delay: .6,
           ease : Power3.easeOut,
        }
     )

      TweenMax.to(
        Animate5,
        {
           opacity:1,
           duration:5,
           y: -30,
           delay: .8,
           ease : Bounce.In,
        }
     )

        gsap.from('.macbookimage', {
      duration: 2,
      y: '100',
      opacity: 0,
      ease: 'bounce',
      scrollTrigger: {
      trigger: '.macbookimage',
      start: 'top 90%',
      end: 'bottom 90%',
      toggleActions: 'restart complete reverse reset',
      },
    });

     gsap.from('.secondaiimage', {
      duration: 2,
      y: '100',
      opacity: 0,
      ease: 'bounce',
      scrollTrigger: {
      trigger: '.secondaiimage',
      start: 'top 90%',
      end: 'bottom 90%',
      toggleActions: 'restart complete reverse reset',
      },
    });

       gsap.from('.supportimage', {
      duration: 2,
      y: '100',
      opacity: 0,
      ease: 'bounce',
      scrollTrigger: {
      trigger: '.supportimage',
      start: 'top 90%',
      end: 'bottom 90%',
      toggleActions: 'restart complete reverse reset',
      },
    });

   }, [])

   return (  
      <> 
      <div className="container"> 
      <div className="Hero" > 
         <h1> <span className="Heroyellowtext"> <span className="a1" ref={txt => { Animate1 = txt}}> Building </span>  <span className="a2" ref={txt => { Animate2 = txt}}> Sustainable </span> <br/> <span className="a3" ref={txt => { Animate3 = txt}}> Software </span> </span>  <span className="Herobluetext"> <span className="a4" ref={txt => { Animate4 = txt}}> Accross </span> <br/> <span className="a5" ref={txt => { Animate5 = txt}}> Borders. </span> </span> </h1>
      </div>
      <p className="herotext"> We take local ideas to the global stage by building sustainable <br/>software for ease. How about that !</p>
      <button className="btn"> Explore </button>


      <Cardcontent/> 
      {/*Software development section*/}

      <div>
         <div class="sdimage"> 
         <img src={sdimage} alt="" className="macbookimage"/>
         <div class="clr"> </div> 
         <div> 

            <div class="sdtitle">
            <h2> <span className="software"> Software </span> Development </h2>
            {/* <p> We build products that are aimed towards carrying out a series of tasks with very 
            little human effort and an easy to use user interface that can be navigated by 
            Non-techies.</p>  */}
            <p> Each product we launch carries out multiple tasks with very little human input and easy-to-use interfaces for non-techies.  </p>
            <hr/>
         </div>
         </div> 
         </div>
      </div> 


        {/*Software development section*/}

      <div>
         <div class="world">
            <div class="worldflex"> 

            <div class="worldimage"> 
         <img src={world} alt="" />
          </div>

          <div class="arrow"> 
         <img src={arrow} alt="" width="30%" />
          </div>

          </div>
         
         <div> 
            <div class="worldtitle">
            <h2> <span className="cross"> Cross </span> borders </h2>
            <p> We believe indigenous innovation from Africa is on-demand across Africa and we step in to take it to the global stage..</p> 
            <hr/>
         </div>
         </div> 
         </div>
      </div>

       {/*Artificial Intelligence section*/}

      <div>
         <div class="aiimage"> 
         <img src={robot} alt="" className="firstaiimage"/>
         <div class="clr"> </div> 
         <div> 

            <div class="aititle">
               <div class="aiimageflex"> 
               <div class="aiimage2"> 
               <img src={robot2} alt="" width="75%" className='secondaiimage'/>
               </div>
               <div className="airightflex"> 
            <h2> <span className="artificial"> Artificial </span> <br/> Intelligence </h2>
            {/* <p> We build products that are aimed towards carrying out a series of tasks with very 
            little human effort and an easy to use user interface that can be navigated by 
            Non-techies.</p>  */}
            <p> With special AI technology, individuals and small and large enterprises will be able to carry out heavy tasks with little effort. </p> 
            <hr/>
            </div> 
            </div>
         </div>
         </div> 
         </div>
      </div> 

      <div className="os"> 

      <div className="osFlex"> 

      <div className="os"> 
      <img src={outsource}/>
      </div>

      <div className="ostext"> 
      <h2> Outsourcing </h2> 
      <p> Are you a startup with an idea, but not
           a team? That's okay, you can hire some
          of the best developers, designers and
          engineers in Africa from Fexteller to 
          work on bringing your ideas to life. </p> 
      </div>
      </div>
      <hr/>
      </div> 
      
      <div class="clr"> </div>

       {/* This is for support */}

       <div>
         <div class="support">

            <div class="supportimage"> 
         <img src={support} alt="" width="50%"/>
          </div>
         
         <div> 
            <div class="supporttitle">
            <h2>  Support </h2>
            <p> Fextellars team players are ready to provide support on whatever your IT needs may be. And we will guide you every step of the way.  </p> 
            
         </div>
         </div> 
         </div>
      </div>

       {/* This is the end of support */}

       </div>

      <Footer/> 
    
      </>
   )
}

   const Cardcontent= () => {
      return(
         <> 
         <div className="cardflex">
         {Cards.map((card) => {
            const {Img, Title, Description} = card;
            return (
            <div className="card"> 
            <div className="cardinnerflex"> 
            <div  className="laptop" > 
               <p> {Img} </p>
              
            </div>
          
            <div className="title"> 
              <h3> {Title} </h3>
            </div>
            </div> 
            {/* end of card inner flex */}


            <div className="carddetails"> 
                 <p> {Description} </p>
            </div>
         </div>

      )
      }
      )
      }
      </div> 
      </> 
            )
         }

export default Homepage; 