import React from 'react'
import { useState } from "react";
import "./Products.css"
import Form from "./Form.js"
import Notified from "./images/bell.gif"
import Footer from './Footer.js'
 


const Products = () => {

    const [Value, setValue] = useState({
      firstName:'',
      lastName:'',
      Email:'',
      Tel:'',
   })

   const handleClick  = (e) => {
      setValue (Value => ({
         ...Value, 
         [e.target.name]: e.target.value
      }))
   }
   
  return (
     <>
     <div className="pbg"> 
      <div className="pflex">
         <div className="ptext"> 
         <h2> We are some working on some products for you. </h2>
         <p> Be the first to be notified when we lauch.</p>
         </div>
         
         <div className="pimage">
         <img src={Notified} alt="" width="100%"/> 
         </div>
      </div>
      </div> 


       {/* Form  */}

       
         <div className="container">
      <div className="containerbg">        
       <div className="Form">
         <h1> Send us an Email </h1>
         <p> You will the the first to know when we launch </p> 
         
          <Form  className="formname" Name="firstName" type="text" placeholder="First Name" value="Value" handleClick={handleClick}/> 
          
           <Form  className="formname" Name="lastName" type="text" placeholder="Last Name" value="Value" handleClick={handleClick}/> 
           
           {/* End of input flex */}
         <Form  className="formemail" Name="Email" type="text" placeholder="Email" value="Value"  handleClick={handleClick}/> 
         <Form  className="formtext" Name="Tel" type="text" placeholder="Phone number" value="Value"handleClick={handleClick}/>
       
      
     </div>
     </div>
     </div> 

         

      <Footer/> 

     </> 
  )
}

export default Products